export default function createCard(
  url,
  likes,
  views,
  comments,
  downloads,
  tags,
  largeImg
) {
  return `
    <div class="photo-card">
     <a href="${largeImg}">
        <img class="gallery__img" src="${url}" alt="${tags}" />
        </a>
        <div class="info">
            <p class="info-item">
                <b>Likes<br>${likes}</b>
            </p>
            <p class="info-item">
                <b>Views<br>${views}</b>
            </p>
            <p class="info-item">
                <b>Comments<br>${comments}</b>
            </p>
            <p class="info-item">
                <b>Downloads<br>${downloads}</b>
            </p>
        </div>
    </div>
            `;
}
